.sideLogo {
    margin-top: 2em;
    margin-bottom: 2em;
    width: 4.5em;
    height: 1em;
}

.singOutIcon {
    width: 20px;
    height: 20px;
    font-size: 1.5rem;
    color: grey;
    position: absolute;
    bottom: 2.5rem;
    left: 2rem;
    text-align: center;
    vertical-align: bottom;
}

.sideHover {
    font-size: 1.5em;
    /* font-family: 'Nunito', sans-serif; */
    color: grey;

    margin-bottom: 0;

    padding: 1rem 1.5rem 1rem 1.5rem;

    text-align: center;
    -webkit-transition: .5s;
    /* ...and now for the proper property */
    transition: .5s;
}

.sideHover:hover {
    background-color: #dddddd;
}
.ITAtlasText {
  margin-top: 0.7em;
  margin-left: 1.5em;
  font-size: 2em;
  color: #606060;
  font-family: 'Lato', sans-serif;
}

.stateText {
  margin-top: 1em;
  margin-left: 1.5em;
  font-size: 1.5em;
  color: #606060;
  font-family: 'Lato', sans-serif;
}

.blueGrad {
  background: -webkit-linear-gradient(left, #33ccff 0%, #0066cc 83%);
  background: linear-gradient(to right, #33ccff 0%, #0066cc 83%);
}

.shareButton {
  background: -webkit-linear-gradient(left, #33ccff 0%, #0066cc 83%);
  background: linear-gradient(to right, #33ccff 0%, #0066cc 83%);
  height: 46px;
  position: absolute; 
  right: 25px;
}

.userIcons {
  position: absolute; 
  right: 150px;
  margin-top: 14px;
}

.accountdropdown{
  padding-left: 50px;
  padding-top: 15px;
}

.dropdownColor {
  background: -webkit-linear-gradient(left, #ffcc00 0%, #ff9900 50%);
  background: linear-gradient(to right, #ffcc00 0%, #ff9900 50%);
}

.avatar {
  position: absolute;
  right: 175px;
  padding-top: 15px;
}
Body {
  background-color: #f5f5f5 !important;
  overflow-x: hidden;
}


